.trns{
  position: relative;
  animation-name: example;
  animation-duration: 1s;
  animation-iteration-count: 1;
}

@keyframes example {
from{
   left: 600px;top: 30px;

}
top{
  left: 30px;
  top: 0px;
}

}
.transone{
  position: relative;
  animation-name: jacob;
  animation-duration: 1s;
}
@keyframes jacob {
   from{
    top:200px;left: 20px;
   }
   to{
    left: 0px;
    top: 0px;
   }
}
.transtwo{
  position: relative;
  animation-name: inter;
  animation-duration: 1s;
}
@keyframes inter {
  from{
    left: 600px;top: 0px;
 
 }
 top{
   left: 0px;
   top: 0px;
 }
  
}
.read-m{
  position: relative;
  animation-name: btn;
  animation-duration: 1s;
}
@keyframes btn {
  from{
    right: 440px; top: 0px;
  }
  to{
    left: 0px;
    top: 0px;
  }
  
}
